<template>
    <div class="container">
        <div class="loading" data-splitting>4 0 4</div>
        <h1 class="opentext">Oops！&nbsp;&nbsp;您访问的页面开小差啦</h1>
        <br/>
        <p :class="['tips', { 'show': showTips }]">
            让我们 <a @click="goBack">返回</a> 继续探索或 <a @click="goIndex">重新开始</a> 吧 
            &nbsp;&nbsp;<span>:D</span>
        </p>
    </div>
</template>

<script>
import textSplit from '../../effects/css3/textsplit/textSplit.js';
import textOpen from '../../effects/css3/textopen/textOpen.js';

export default {
    data() {
        return {
            showTips: false
        };
    },
    methods: {
        goBack() {
            history.go(-1);
        },
        goIndex() {
            this.$router.push('/');
        },
        showTipsAnimation() {
            this.showTips = true;
        }
    },
    mounted() {
        textSplit();
        textOpen();
        this.showTipsAnimation();
    }
}
</script>

<style lang="scss" scoped>
    .container {
        
        h1 {
            color: whitesmoke;

            &::before, &::after { background-color: white; }
        }

        p {
            font-size: 16px;
            color: #0043a7;
            font-weight: bold;
            
            a {
                cursor: pointer;
                color: #003079;
                
                &:hover { border-bottom: 1px solid #003079; }
            }

            span { 
                font-weight: normal;
                font-size: 40px; 
            }
        }
    }

    .tips {
        opacity: 0;
        transition: opacity 2s ease-in-out;

        &.show { opacity: 1; }
    }
</style>

<style lang="scss">
    .loading .char {
        font-size: 56px;
        
        &:before, &:after { color: #F56C6C;}
    }
</style>


